.teachersBlock {
	background: radial-gradient(110% 166.99% at 100% 0%, #F1C04B 0%, rgba(209, 80, 255, 0.277269) 62.18%, rgba(255, 255, 255, 0) 100%), #443264;
	padding-top: 50px;
	padding-bottom: 50px;
	&__title {
		font-family: $blogger;
		font-weight: 700;
		font-size: $big-font-size;
		line-height: 33px;
		text-transform: uppercase;
		margin: 0;
		margin-bottom: 50px;
		color: $white;
		text-align: center; } }
.teachersSlider {
	width: 100%;
	height: 300px;
	margin-bottom: 50px;
	background: transparent;
	.swiper-button-prev, .swiper-button-next {
		top: 30%;
		transform: translateY(-30%);
		height: 82px;
		width: 44px;
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	.swiper-button-next {
		background-image: none;
		right: 0; }
	.swiper-button-prev {
		background-image: none;
		left: 0; } }
.teachersSlide {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	&__img {
		border: 2px solid $white;
		overflow: hidden;
		border-radius: 50%;
		width: 170px;
		height: 170px;
		img {
			display: block;
			height: 100%;
			width: 100%;
			border-radius: 50%;
			object-fit: cover; } }
	&__text {
		margin-top: 30px;
		span {
			font-family: $roboto;
			font-weight: normal;
			font-size: 20px;
			color: $white;
			display: block;
			text-align: center;
			padding-top: 10px; } } }
