.footer {
	padding: 50px 0px;
	background: $violet; }
.footerContainer {
	@include row-flex();
	justify-content: space-between;
	&__left {
		@include col();
		@include size(5);
		@include size-xl(4);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		.btn-product {
			margin-bottom: 50px; } }
	&__brand {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: 35px;
		&-logo {
			margin-right: 25px;
			height: 35px;
			text-align: center;
			img {
				width: 100%;
				height: 100%;
				object-fit: contain; } }
		&-phone {
			display: flex;
			align-items: center;
			text-align: center;
			@include xl(margin-top, 20px);
			img {
				height: 30px;
				width: 30px;
				margin-top: 5px;
				margin-right: 10px; }
			span {
				font-family: $blogger;
				font-weight: 700;
				font-size: $big-font-size;
				color: $white; } } }
	&__social {
		&-title {
			font-family: $roboto;
			font-weight: normal;
			font-size: $base-font-size;
			color: $white;
			line-height: 19px;
			margin: 0 0 10px 0; }
		&-img {
			display: flex;
			align-items: center;
			justify-content: center;
			a {
				margin-right: 10px;
				&:last-child {
					margin-right: 0; } } } }
	&__right {
		@include col();
		@include size(7);
		@include size-xl(8);
		display: flex;
		justify-content: flex-end; }
	&__menu {
		font-family: $roboto;
		font-size: $base-font-size;
		display: flex;
		align-items: flex-start;
		justify-content: center;
		text-align: center;
		li {
			margin-right: 15px;
			a {
				border-right: 2px solid $white;
				padding-right: 15px; }
			&:last-child {
				a {
					border-right: none; } }
			ul {
				display: flex;
				flex-direction: column;
				align-items: center;
				li {
					margin-top: 20px;
					a {
						width: 100%;
						border-right: none; } } } }
		a {
			display: block;
			color: $white; } } }
