.contactsBlock {
	padding-top: 50px;
	padding-bottom: 50px;
	&__title {
		font-family: $blogger;
		font-size: $big-font-size;
		font-weight: 700;
		line-height: 33px;
		text-transform: uppercase;
		text-align: center;
		color: $purple; } }
.contactsAddressContainer {
	display: flex;
	justify-content: space-around;
	align-items: center; }
.contactsAddress {
	margin-top: 50px;
	&_purple {
		color: $purple; }
	&_orange {
		color: $orange; }
	span {
		display: block;
		font-family: $blogger;
		font-size: $big-font-size;
		font-weight: 700;
		line-height: 33px;
		text-transform: uppercase;
		font-style: normal;
		text-align: center; } }
