.schoolCartBlock {
	position: relative;
	padding-top: 50px;
	padding-bottom: 50px;
	z-index: 5; }
.schoolCartContainer {
	@include row-flex();
	justify-content: space-around;
	align-items: flex-start; }
.schoolCart {
	@include col();
	@include size(3.1);
	position: relative;
	font-family: $blogger;
	height: 430px;
	.btn-product {
		width: 100%;
		position: absolute;
		bottom: 0; }
	&__text {
		position: relative;
		height: 185px;
		border-radius: 10px;
		h4 {
			display: block;
			text-align: right;
			font-size: $big-font-size;
			font-weight: 700;
			margin: 0;
			text-transform: uppercase;
			line-height: 33px;
			padding: 18px 17px 0 0;
			color: $white; }
		span {
			display: block;
			text-align: right;
			font-weight: 400;
			line-height: 33px;
			font-size: $large-font-size;
			padding: 0 17px 20px 0;
			color: $white; }
		&_green {
			background: $green; }
		&_yellow {
			background: $yellow; }
		&_purple {
			background: $purple; } }
	&__img {
		position: absolute;
		&_green {
			top: 60px;
			left: -50px;
			width: 121;
			height: 303px; }
		&_yellow {
			top: 173px;
			left: -20px;
			width: 229;
			height: 170px; }
		&_purple {
			top: 106px;
			left: 30px;
			width: 229px;
			height: 249px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__header {
		position: relative;
		height: 50px; }
	&__starGreen {
		position: absolute;
		&_one {
			left: 20%;
			bottom: 15%;
			width: 57px;
			height: 57px; }
		&_two {
			left: 45%;
			top: 2%;
			width: 19px;
			height: 19px; }
		&_three {
			right: 6%;
			bottom: 4%;
			width: 35px;
			height: 35px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__circleGreen {
		position: absolute;
