.faqBlock {
	position: relative;
	padding-top: 50px;
	padding-bottom: 50px;
	&__title {
		font-family: $blogger;
		font-weight: 700;
		font-size: $big-font-size;
		line-height: 33px;
		margin: 0;
		text-align: center;
		color: $orange;
		text-transform: uppercase; } }
.faqContainer {
	position: relative;
	@include row-flex;
	justify-content: space-between;
	align-items: flex-start;
	&__ask {
		position: absolute;
		z-index: -1;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; }
		&_small {
			top: -60px;
			left: 40px;
			width: 66px;
			height: 80px; }
		&_large {
			top: -100px;
			left: 180px;
			width: 233px;
			height: 252px; }
		&_medium {
			top: -85px;
			right: 18%;
			width: 124px;
			height: 135px; }
		&_big {
			top: -170px;
			right: -10%;
			height: 417px;
			width: 233px; } } }
.faqCart {
	@include col();
	@include size(3);
	margin-top: 35px;
	&__title {
		background: $orange;
		border-radius: 10px;
		height: 110px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center; }
	&__heading {
		font-family: $blogger;
		font-size: $medium-font-size;
		font-weight: 700;
		line-height: 22px;
		text-transform: uppercase;
		color: $white;
		text-align: center;
		padding: 20px 30px;
		margin: 0; }
	&__text {
		border: 2px solid #33A977;
		box-sizing: border-box;
		border-radius: 10px;
		margin-top: 30px; }
	&__subtitle {
		font-family: $blogger;
		font-size: $medium-font-size;
		line-height: 22px;
		font-weight: 700;
		text-transform: uppercase;
		text-align: center;
		margin: -24px auto 0 auto;
		color: $green;
		background: $white;
		padding: 10px;
		width: 100px; }
	&__descr {
		font-family: $roboto;
		font-weight: normal;
		font-size: $small-font-size;
		line-height: 20px;
		padding: 16px 25px 38px 25px;
		color: $grey;
		margin: 0; }
	&__btnWrapp {
		width: 220px;
		background: $white;
		margin: 0 auto -25px auto; }
	.btn-product {
		width: 200px;
		margin: 0 auto; } }
