@import "modules/blocks/color-circle.sass";
.headerContainer {
	position: relative;
	padding-left: 30px;
	padding-right: 30px;
	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__brand {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 5;
		&-logo {
			margin-right: 25px;
			height: 35px;
			text-align: center;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; } }
		&-phone {
			display: flex;
			align-items: center;
			text-align: center;
			img {
				height: 30px;
				width: 30px;
				margin-top: 5px;
				margin-right: 10px; }
			span {
				font-family: $blogger;
				font-weight: 700;
				font-size: $big-font-size;
				color: $white; } } } }
